input[type=checkbox] {
  position: relative;
  cursor: pointer;
  width: 0;
  height: 0;
  margin: 0;
}

input[type=checkbox]:before {
  content: "";
  display: block;
  position: absolute;
  width: 16px;
  height: 16px;
  top: -12px;
  left: -15px;
  border: 2px solid #777D74;
  border-radius: 3px;
  background-color: white;
}
input[type=checkbox]:checked:after {
  content: "";
  display: block;
  width: 5px;
  height: 10px;
  border: solid #777D74;
  border-width: 0 2px 2px 0;
  transform: rotate(45deg);
  position: absolute;
  top: -11px;
  left: -8px;
}

